import cpimg from "@/components/cpimg.vue"
export default {
	data() {
		return {
			src:'',
			data:Array
		};
	},
	methods: {
		// 添加图片
		addPhoto() {
			let that = this
			that.$refs.cpimg._changImg()
		},
		cpimgOk(file) {
			let that = this
			that.src = file
			console.log(file)
			uni.request({
				url: 'https://www.qinendong.com/api/image_recognition/Dish_recognition', //仅为示例，并非真实接口地址。
					data: {
						id: file.replace("data:image/png;base64,","").replace("data:image/jpeg;base64,","")
					},
					method:"POST",
				header: {
					'Content-type':'application/x-www-form-urlencoded' //自定义请求头信息
					},
					success: (res) => {
								console.log(res.data.data);	
								this.data =  JSON.parse(res.data.data)
								console.log(this.data)
					}
				});
			
		},
		cpimgErr(e) {
			console.log(e)
		},
	},
	components: {
		cpimg
	},
}
